}

body {
  color: var(--description-text-color);
  background: var(--main-background-color);
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

.hide-title {
  clip: rect(0 0 0 0);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 1216px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 32px;
  padding-right: 32px;
}

.blob-btn {
  z-index: 1;
  text-align: center;
  text-transform: uppercase;
  color: var(--action-text-color);
  border: 2px solid var(--action-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  outline: none;
  margin-bottom: 30px;
  padding: 13px 41px;
  transition: color .5s;
  position: relative;
}

.blob-btn__add {
  text-transform: uppercase;
  color: var(--action-text-color);
  border: 2px solid var(--rating-background-color);
  background-color: var(--rating-background-color);
  border-radius: 5px;
  padding: 13px 41px;
}

.blob-btn-modal, .blob-btn__add-modal {
  padding: 13px 10px;
}

.blob-btn:hover {
  color: var(--action-text-color);
  border: 2px solid var(--rating-background-color);
  border-radius: 8px;
}

.blob-btn__inner {
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.blob-btn__blob {
  width: 25%;
  height: 100%;
  background: var(--rating-background-color);
  border-radius: 100%;
  transition: transform .45s;
  position: absolute;
  top: 2px;
  transform: translate3d(0, 150%, 0)scale(1.7);
}

.blob-btn__blob:nth-child(1) {
  transition-delay: 0s;
  left: 0%;
}

.blob-btn__blob:nth-child(2) {
  transition-delay: 80ms;
  left: 30%;
}

.blob-btn__blob:nth-child(3) {
  transition-delay: .16s;
  left: 60%;
}

.blob-btn__blob:nth-child(4) {
  transition-delay: .24s;
  left: 90%;
}

.blob-btn:hover .blob-btn__blob {
  transform: translateZ(0)scale(1.7);
}

